.About {
  animation-name: fadeIn;
  animation-duration: 500ms;
  height: 100vh;

  &__Founders {
    margin: 50px auto 20px auto;
    height: 600px;
    background-color: #2c2c2c;
    width: 80%;
    display: grid;
    padding: 50px 0;
    grid-template-columns: 1fr 1fr 1fr;

    &--founder {
    }

    &--photo {
      height: 250px;
      width: 250px;
      border: 1px solid grey;
      margin: 0 auto;
      border-radius: 200px;
    }

    *--info {
    }
  }
}
